}
.button-wraper{
    margin-top: 13vw;
    display: flex;
    flex-direction: column;
}
.red{
    width: 4.72vw;
    background: #DB4437;
    color: white;
}
.yellow{
    width: 8.76vw;
    background: #F4B400;
    color: black
}
.titlePart{
